import { Link } from "gatsby";
import React from "react";

const PostHashtags = ({
  keywords,
  readMoreTitle,
}: {
  keywords: string[];
  readMoreTitle: string;
}) => {
  return (
    <div className="w-full">
      <p className="text-lg font-bold mb-5">{readMoreTitle}</p>
      <div className="flex flex-wrap gap-2">
        {keywords.map((hashtag) => (
          <Link
            key={hashtag}
            to={`/blog/${hashtag.split(" ").join("-").toLowerCase()}`}
            title={`blog about ${hashtag}`}
            className="text-sm text-secondary-10 border border-solid border-secondary-10 rounded-full px-5 py-2.5 hover:border-secondary-100 hover:text-secondary-100 hover:bg-main-70"
          >
            #<span itemProp="keywords">{hashtag}</span>
          </Link>
        ))}
      </div>
    </div>
  );
};

export default PostHashtags;
