import React from "react";
import { Link } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

import { IPostAuthor } from "../../types/post.type";

const PostAuthorCard = ({
  authorAlias,
  author,
  authorPosition,
  authorPhoto,
}: IPostAuthor) => {
  const avatar = getImage(authorPhoto);

  return (
    <Link
      to={`/blog/${authorAlias.split(" ").join("-").toLowerCase()}`}
      title={`${author}'s blog`}
      itemProp="author"
      itemScope
      itemType="https://schema.org/Person"
      className="flex items-center gap-4 mb-8 sm:mb-12 text-secondary-100 text-lg"
    >
      {avatar ? (
        <GatsbyImage
          image={avatar}
          alt="avatar"
          itemProp="image"
          className="object-contain aspect-square rounded-full w-16 grayscale"
        />
      ) : null}
      <div className="flex-1 w-max">
        <p itemProp="name" className="font-semibold whitespace-nowrap">
          {author}
        </p>
        <p itemProp="jobTitle" className="text-secondary-30">
          {authorPosition}
        </p>
      </div>
    </Link>
  );
};

export default PostAuthorCard;
