import React from "react";
import { HeadFC, Link, PageProps, graphql } from "gatsby";
import { useLocation } from "@reach/router";

import { IPostPageContext, IPostQueryResult } from "../types/post.type";

import { HeadSeo, LocalBusinessMetadata } from "../components/common/metadata";
import ContentContainer from "../components/common/contentContainer";
import ShareButtons from "../components/posts/shareButtons";
import PostFooter from "../components/posts/postFooter";
import PostHashtags from "../components/posts/postHashtags";
import PostAuthorCard from "../components/posts/postAuthorCard";
import Comments from "../components/posts/comments";

const Post = ({
  data: {
    post: {
      frontmatter,
      html,
      wordCount: { words },
    },
    author: authorData,
  },
  pageContext: { markupData, lang, site },
}: PageProps<IPostQueryResult, IPostPageContext>) => {
  const { href: currentUrl } = useLocation();

  const { date, title, keywords } = frontmatter;
  const { authorAlias, author, authorPosition, authorPhoto } =
    authorData.frontmatter;

  const minutes = Math.floor(words / 200);
  const readingTime = minutes ? minutes : 1;

  return (
    <>
      <div
        itemScope
        itemType="https://schema.org/Article"
        className="flex bg-main-100"
      >
        <ContentContainer
          maxWidthClass="max-w-2xl"
          className="flex-col text-secondary-100 text-lg md:!px-0 pt-8 sm:pt-16"
        >
          <PostAuthorCard
            authorAlias={authorAlias}
            author={author}
            authorPhoto={authorPhoto}
            authorPosition={authorPosition}
          />

          <div className="w-full flex items-center justify-between mb-2.5 sm:mb-5 text-sm sm:text-base">
            <span itemProp="datePublished">{date}</span>

            <span>
              {markupData.readingTimeTitle}: {readingTime}
              {markupData?.readingTimeUnits}.
            </span>
          </div>

          <h1
            className="text-[24px] sm:text-4xl font-semibold mb-8"
            itemProp="headline"
          >
            {title}
          </h1>

          <article
            className="single-post"
            dangerouslySetInnerHTML={{ __html: html }}
            itemProp="articleBody"
          />

          <div className="flex flex-col sm:flex-row w-full gap-8 lg:gap-16 mb-8 md:mb-12">
            {keywords ? (
              <PostHashtags
                keywords={keywords}
                readMoreTitle={markupData.readMoreOnTopicTitle}
              />
            ) : null}

            <ShareButtons
              url={currentUrl}
              postTitle={title}
              shareTitle={markupData.sharePostTitle}
            />
          </div>

          <PostFooter
            title={markupData.postFooterTitle}
            text={markupData.postFooterText}
          />

          <Comments setup={site.commentsSetup} />
        </ContentContainer>
        <LocalBusinessMetadata itemProp="copyrightHolder" lang={lang} />
      </div>
    </>
  );
};

export default Post;

export const Head: HeadFC<IPostQueryResult, IPostPageContext> = ({
  data: {
    post: {
      frontmatter: { title, text },
    },
  },
  pageContext,
  location,
}) => (
  <HeadSeo
    title={`${pageContext.markupData.head} | ${title.toUpperCase()}`}
    siteUrl={pageContext.site.siteUrl}
    description={`${text.split(" ").slice(0, 22).join(" ")}...`}
    path={location.pathname}
    lang={pageContext.lang}
  />
);

export const query = graphql`
  query ($id: String, $lang: String, $authorId: String) {
    post: markdownRemark(id: { eq: $id }) {
      frontmatter {
        date(formatString: "DD MMM YYYY", locale: $lang)
        title
        text
        keywords
      }
      html
      wordCount {
        words
      }
    }
    author: markdownRemark(id: { eq: $authorId }) {
      frontmatter {
        authorAlias
        author
        authorPosition
        authorPhoto {
          childImageSharp {
            gatsbyImageData(
              width: 64
              formats: [AUTO, WEBP, AVIF]
              placeholder: BLURRED
            )
          }
        }
      }
    }
  }
`;
