import React from "react";
import { graphql, useStaticQuery } from "gatsby";
import { GatsbyImage, ImageDataLike, getImage } from "gatsby-plugin-image";

const PostFooter = ({ title, text }: { title: string; text: string }) => {
  const fileData = useStaticQuery(query) as { file: ImageDataLike | null };
  const image = getImage(fileData?.file);

  return (
    <div className="flex flex-col sm:flex-row items-center w-full sm:px-12 mb-8 gap-5 justify-center">
      {image ? (
        <GatsbyImage
          image={image}
          alt="like"
          className="object-contain aspect-square h-40"
        />
      ) : null}
      <div className="flex-1">
        <p className="text-lg font-bold mb-5">{title}</p>
        <p className="text-base">{text}</p>
      </div>
    </div>
  );
};

export default PostFooter;

export const query = graphql`
  query {
    file(name: { eq: "postFooterImage" }) {
      childImageSharp {
        gatsbyImageData(
          formats: [AUTO, WEBP, AVIF]
          placeholder: BLURRED
          width: 160
        )
      }
    }
  }
`;
