import React from "react";

import X from "../../assets/posts/X.svg";
import { FaPinterest } from "@react-icons/all-files/fa/FaPinterest";
import { FaLinkedin } from "@react-icons/all-files/fa/FaLinkedin";
import { FaGoogle } from "@react-icons/all-files/fa/FaGoogle";
import { FaFacebook } from "@react-icons/all-files/fa/FaFacebook";

const ShareButtons = ({
  url,
  postTitle,
  shareTitle,
}: {
  url: string;
  postTitle: string;
  shareTitle: string;
}) => {
  return (
    <div className="sm:basis-52 sm:shrink-0">
      <p className="text-lg font-bold mb-5">{shareTitle}</p>

      <div className="flex w-full items-center justify-evenly sm:justify-between py-2.5">
        <ShareButton
          media="Twitter"
          url={`http://twitter.com/share?text=${postTitle}&url=${url}`}
        >
          <X />
        </ShareButton>
        <ShareButton
          media="Pinterest"
          url={`http://pinterest.com/pin/create/button/?url=${url}&description=${postTitle}`}
        >
          <FaPinterest size="20px" />
        </ShareButton>
        <ShareButton
          media="Linkedin"
          url={`http://www.linkedin.com/shareArticle?mini=true&url=${url}&title=${postTitle}&source=CodeCave`}
        >
          <FaLinkedin size="20px" />
        </ShareButton>
        <ShareButton
          media="Google+"
          url={`https://plus.google.com/share?url=${url}`}
        >
          <FaGoogle size="20px" />
        </ShareButton>
        <ShareButton
          media="Facebook"
          url={`https://www.facebook.com/sharer/sharer.php?u=${url}&p[title]=${postTitle}`}
        >
          <FaFacebook size="20px" />
        </ShareButton>
      </div>
    </div>
  );
};

export const ShareButton = ({
  url,
  media,
  children,
}: React.PropsWithChildren<{ url: string; media: string }>) => {
  return (
    <a
      href={url}
      rel="noopener noreferrer"
      target="_blank"
      title={`Share on ${media}`}
      className="hover:brightness-75"
    >
      {children}
    </a>
  );
};

export default ShareButtons;
